.filme-info {
    display: flex;
    flex-direction: column;
    max-width: 900px;
}
.filme-info img {
    border-radius: 20px;
}
.filme-info button {
    margin-right: 5px;
    margin-top: 15px;
    cursor: pointer;
    border-radius: 5px;
    border: 0;
    font-size: 20px;
    outline: none;
    padding: 12px;
    transition: all 0.5;
}
.filme-info button:hover {
    background-color: rgb(187, 21, 21);
    color: #fff;
}
.filme-info a {
    text-decoration: none;
    color: black;
}
.btnSalvar {
    color: #000;
}